<template>
  <div class="middle">
    <transition  enter-active-class="animated bounceInLeft"
    leave-active-class="animated bounceOutRight"
       mode="out-in"        >
<router-view v-if="!$route.meta.keepAlive"></router-view>
    </transition>
  </div>
</template>
<script>
 import animate from 'animate.css'
export default {
 name:"middle"
}

</script>
<style scoped>
.middle{
    
    height: 100%;
    display: flex;
    justify-content:center;
    
}
</style>





